.login {
  width: 100vw;
  height: 100vh;
  background: linear-gradient(
      to bottom,
      rgba(0, 0, 0, 0) 0%,
      rgba(0, 0, 0, 1) 100%
    ),
    url("http://mppmduse2pmpovwapp.azurewebsites.net/wp-content/uploads/2019/09/netflix-background-9.jpg");
  background-size: cover;
  position: relative;
}

.login .top .wrapper {
  padding: 20px 50px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.login .top .wrapper .logo {
  height: 40px;
}

.login .container {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: white;
}

.login .container form {
  width: 300px;
  height: 300px;
  padding: 30px;
  border-radius: 5px;
  background-color: var(--main-color);
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}

.login .container form input {
  height: 40px;
  border-radius: 5px;
  background-color: gray;
  color: white;
  padding-left: 10px;
}

.login .container form input::placeholder {
  color: lightgray;
}

.login .container form button {
  height: 45px;
  border-radius: 5px;
  background: linear-gradient(to right, #ff416c, #ff4b2b); /* Gradient background */
  color: white;
  border: none;
  font-size: 18px;
  font-weight: 600;
  cursor: pointer;
  text-transform: uppercase;
  transition: background-color 0.3s ease, transform 0.3s ease, box-shadow 0.3s ease;
}

.login .container form button:hover {
  background: linear-gradient(to right, #ff4b2b, #ff416c); /* Gradient on hover */
  box-shadow: 0 5px 15px rgba(255, 65, 108, 0.5); /* Shadow effect */
  transform: translateY(-5px); /* Slight lift on hover */
}

.login .container form span {
  color: lightgray;
}

.login .container form span b {
  color: white;
}

.red {
  color: red;
}

* {
  text-decoration: none;
}